'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { useEffect } from 'react'
import { useParams } from 'next/navigation'

import { FaHeart, FaComment, FaShare } from 'react-icons/fa'

export default function VideoDetailPage() {
  const params = useParams()
  const videoId = params.videoId
  const [videoUrl, setVideoUrl] = useState('')
  const [likes, setLikes] = useState(0)
  const [comments, setComments] = useState<string[]>([])
  const [newComment, setNewComment] = useState('')
  const [shared, setShared] = useState(false)

  useEffect(() => {
    // Construct video URL based on videoId
    // Assuming video files are served from /uploads/videos/
    setVideoUrl(`/uploads/videos/${videoId}`)
  }, [videoId])

  function handleLike() {
    setLikes(likes + 1)
  }

  function handleAddComment() {
    if (newComment.trim() === '') return
    setComments([...comments, newComment.trim()])
    setNewComment('')
  }

  function handleShare() {
    // For demo, just toggle shared state
    setShared(true)
    setTimeout(() => setShared(false), 2000)
  }

  return (
    <div className="min-h-screen p-6 max-w-3xl mx-auto bg-white rounded-xl shadow-lg">
      <video
        src={videoUrl}
        controls
        autoPlay
        className="w-full rounded-lg mb-6 shadow-md"
      />

      <div className="flex items-center justify-around mb-6 text-black">
        <button
          onClick={handleLike}
          aria-label="Like"
          className="flex items-center space-x-2 hover:text-gray-700 transition"
        >
          <FaHeart size={24} />
          <span>{likes}</span>
        </button>

        <button
          onClick={handleShare}
          aria-label="Share"
          className="flex items-center space-x-2 hover:text-gray-700 transition"
        >
          <FaShare size={24} />
          <span>{shared ? 'Shared!' : 'Share'}</span>
        </button>
      </div>

      <div className="mb-6">
        <h2 className="text-xl font-semibold mb-2 text-black">Comments</h2>
        <ul className="mb-4 max-h-40 overflow-y-auto border border-gray-300 rounded p-3 space-y-2">
          {comments.length === 0 && <li className="text-gray-600">No comments yet.</li>}
          {comments.map((comment, index) => (
            <li key={index} className="text-black border-b border-gray-200 pb-1">
              {comment}
            </li>
          ))}
        </ul>
        <div className="flex space-x-2">
          <input
            type="text"
            placeholder="Add a comment"
            value={newComment}
            onChange={(e) => setNewComment(e.target.value)}
            className="flex-grow border border-gray-400 rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-black"
          />
          <button
            onClick={handleAddComment}
            className="bg-black text-white px-4 py-2 rounded hover:bg-gray-900 transition"
          >
            Post
          </button>
        </div>
      </div>
    </div>
  )
}
